/* login-main */

#login-main{
	background-color: var(--third-color);
	width: 100%;
	height: 110vh;
	display: flex;
	justify-content: center;
	flex-direction: column;
	align-items: center;
}

/*  login main section */
#login_main_section1{

	width: 80%;
	max-height: 80%;
	display: flex;
	flex-direction: column;
	justify-content: flex-start;
	align-items: flex-start;
}

/*  login main navbar */
#login_main_section1_nav1{
	border: var(--third-color) solid 2px;
	width: 100%;
	height: 13%;
	display: flex;
	flex-direction: row;
	justify-content: flex-start;
	align-items: center;
	margin-bottom: 0px;
}

.login_main_section1_nav1_h3{
	height: 100%;
	width: 49%;
	display: flex;
	justify-content: center;
	align-items: center;
	font-size: 110%;
	background-color: rgb(64, 68, 106);
	color: white;
	margin-right: 1%;
}
.login_main_section1_nav1_h3:last-child{
	border-right: none;
	background-color: rgba(192, 200, 250,0.8);
	color: black;
}

/*  login main article */
#login_main_section1_article1{
	width: 100%;
	height: 20%;
	display: flex;
	flex-direction: row;
	justify-content: flex-start;
	align-items: center;

	margin-top: 1.5%;
}

.login_main_section1_article1_ul{
	height: 100%;
	width: 49%;
	overflow-y: scroll;
	background-color: rgb(64, 68, 106);
	color: white;
	margin-right: 1%;
}
.login_main_section1_article1_ul::-webkit-scrollbar {
  width: 10px;
}
.login_main_section1_article1_ul::-webkit-scrollbar-thumb {
  background: black; /* scrollbar color */
}
.login_main_section1_article1_ul::-webkit-scrollbar-track {
  background: var(--fourth-color); /* track color */
}

.login_main_section1_article1_ul:last-child{
	background-color: rgba(192, 200, 250,0.8);
	color: black;
}
.login_main_section1_article1_ul:last-child::-webkit-scrollbar {
  width: 10px;
}
.login_main_section1_article1_ul:last-child::-webkit-scrollbar-thumb {
  background: black; /* scrollbar color */
}
.login_main_section1_article1_ul:last-child::-webkit-scrollbar-track {
  background: rgb(64, 68, 106); /* track color */
}

/*  login main website admin and board members forms section */
#login_main_section2{
	width: 100%;
	height: 67%;
	display: flex;
	flex-direction: row;
	justify-content: flex-start;
	align-items: center;
	margin-top: 1.5%;
}

/*  website admin form */
#website_admin_form{

	border-right: none;
	width: 49%;	
	height: 100%;
	background-color: rgb(64, 68, 106);
	color: white;
	margin-right: 1%;
		padding: 2%;
		box-sizing: border-box;

}

#website_admin_form_fieldset{
	border: none;
	width: 100%;
	height: 75%;
	border: solid var(--third-color) 2px;
	box-sizing: border-box;
	display: flex;
	justify-content: center;
	align-items: center;
	background-color: var(--third-color);
	border: solid var(--fourth-color) 1px;

}

#website_admin_form_fieldset_legend{
	padding: 1%;
	background-color: var(--third-color);
	border: solid var(--fourth-color) 1px;
}

#website_admin_form_password {
  border: none;              /* Removes default border */
  outline: none;             /* Removes the browser’s blue focus ring */
  background-color: var(--third-color); /* Example background */
  font-size: 16px;
  width: 60%;
  border-bottom: white solid 1px;
  color: white;
  padding-left: 1%;
  margin-right: 2%;
}
#website_admin_form_password:focus {
  border: 1px solid white;  /* Custom focus border (indigo) */
  background-color: rgb(64, 68, 106);
}
#website_admin_form_password:hover {
  background-color: rgb(64, 68, 106);
}

#website_admin_form_password_submit{
	border: none;
	color: white;
	background-color: rgb(64, 68, 106);
	padding: 1%;
	cursor: pointer;
	margin-right: 1%;
}
#website_admin_form_password_submit:hover{
	background-color: white;
	color: var(--third-color);
}

#website_admin_form_password_reset{
	border: var(--third-color) solid 1px;
	color: white;
	padding: 1%;
	cursor: pointer;
	background-color: var(--third-color);
}
#website_admin_form_password_reset:hover{
		border: white solid 1px;
}

#website_admin_form_div1{
	width: 100%;
	height: 25%;

	display: flex;
	justify-content: center;
	align-items: center;
}

#website_admin_form_loading{
		display: none;
}

#website_admin_form_error_msg{
	border: solid white 1px;
	outline: red 3px solid;
	height: 80%;
	width: 80%;
	background-color: black;
	color: white;
	text-decoration: underline;
	text-decoration-color: red;
	display: flex;
	justify-content: center;
	align-items: center;
	display: none;

}


/*  board member form  */
#board_members_form{
	border:var(--third-color)solid 2px;
	width: 50%;
	height: 100%;
	background-color: rgba(192, 200, 250,0.8);
	padding: 2%;
	box-sizing: border-box;
	color: white;
}

#board_members_form_fieldset{
	width: 100%;
	height: 75%;
	box-sizing: border-box;
	display: flex;
	flex-direction: column;
	justify-content: center;
	align-items: center;
	background-color: rgb(64, 68, 106);
	border: solid var(--fourth-color) 1px;
}

#board_members_form_fieldset_legend{
	border: solid white 1px;
	background-color: rgb(64, 68, 106);
	padding: 1%;
}

.board_members_form_fieldset_div{

	width: 100%;
	margin-top: 2%;
	display: flex;
	justify-content: flex-start;
	align-items: center;

}

.board_members_form_fieldset_label{
	width: 25%;
	height: 100%;
	display: flex;
	justify-content: flex-end;
	align-items: flex-end;
	margin-right: 0.5%;
}

.board_members_form_fieldset_label_input{
	width: 75%;
	height: 100%;
  border: none;              /* Removes default border */
  outline: none;             /* Removes the browser’s blue focus ring */
  font-size: 15px;
  border-bottom: white solid 1px;
  color: white;
  padding-left: 1%;
  background-color: rgb(64, 68, 106);
  padding-top: 2%;
  box-sizing: border-box;
}
.board_members_form_fieldset_label_input:focus {
  border: 1px solid white;  /* Custom focus border (indigo) */
  background-color: var(--third-color);
}
.board_members_form_fieldset_label_input:hover {
  background-color: var(--third-color);
}

#board_members_form_fieldset_div_submit_and_reset{
	display: flex;
	justify-content: center;
	align-items: center;

	height: 25%;
}

#board_members_form_password_submit{
	height: 80%;
	width: 20%;
	margin-right: 2%;
	display: flex;
	justify-content: center;
	align-items: center;
	border: solid white 1px;
	background-color: var(--third-color);
	color: white;
	cursor: pointer;
}
#board_members_form_password_submit:hover{
	background-color: white;
	color: black;

}

#board_members_form_password_reset{
	height: 80%;
		width: 20%;
		display: flex;
	justify-content: center;
	align-items: center;
	background-color: rgb(64, 68, 106);
	color: white;
	border: none;
	cursor: pointer;
}
#board_members_form_password_reset:hover{
	border: solid white 1px;
}


#board_members_form_div1{
	width: 100%;
	height: 25%;
	display: flex;
	justify-content: center;
	align-items: center;
}

#board_members_form_loading{
display: none;
}

#board_members_form_error_msg{
	border: solid white 1px;
	outline: red 3px solid;
	height: 80%;
	width: 80%;
	background-color: black;
	color: white;
	text-decoration: underline;
	text-decoration-color: red;
	display: flex;
	justify-content: center;
	align-items: center;
	display: none;

}




